html{
    scroll-snap-type: y mandatory;
}
.App{
    height: 100hv;
    background-color: black;
    display: grid;
    place-items: center;
}

.app__videos{
    position: relative;
    height: 800px;
   
    border-radius: 20px;
    /*width: 80%;*/
    max-width: 1280px;/*max-width: 500px;*/
    overflow: scroll;
    scroll-snap-type: y mandatory;
}

/**coultar crollbar para chrome, safari y opera*/
.app__videos::-webkit-scrollbar{
    display: none;
}

/*ocultar scrollbar para IE, edge y firefox*/
.app__videos{
    -ms-overflow-style: none;
    scrollbar-width: none;
}